<template>
  <div>
    <div class='item'>
      <el-card class='card' shadow="hover">
        <div class='content'>
          <div class='left'>
            <div class='name'>
              {{itemName}} ({{itemPublish}} {{$t('Market.ItemVersion')}} {{itemVersion}})
            </div>

            <div class='class'>
              {{className}} - {{schoolFormatted}}
            </div>

            <div class='status'>
              <span class='statusElement'>
                <el-image class='statusImage' :src="require('@/../public/assets/price.png')"></el-image>
                {{price}}
              </span>

              <el-tooltip :content='this.$t("Market.ItemBroken")' placement='top-start' effect='light'>
                <span class='statusElement'>
                  <el-image class='statusImage' :src="require('@/../public/assets/broken.png')"></el-image>
                  {{brokenFormatted}}
                </span>
              </el-tooltip>

              <el-tooltip :content='this.$t("Market.ItemWritten")' placement='top-start' effect='light'>
                <span class='statusElement'>
                  <el-image class='statusImage' :src="require('@/../public/assets/written.png')"></el-image>
                  {{writtenFormatted}}
                </span>
              </el-tooltip>
            </div>
          </div>

          <div class='picture'>
            <el-image :src="pictureFormatted" fit='fill'></el-image>
          </div>
        </div>

        <div class='actions'>
          <span class='open' @click='showMarketDetail(itemID)'>
            <span>{{$t('Market.DetailOpen')}}</span>
            <i class='el-icon-caret-bottom'></i>
          </span>
        </div>

      </el-card>

    </div>
  </div>

</template>

<script>
import * as server from '../../../server.js'

export default {
  name: 'market-item',
  props: {
    itemID: { type: Number },
    itemName: { type: String },
    itemVersion: { type: String },
    itemPublish: { type: String },
    className: { type: String },
    school: { type: Number },
    tag: { type: Array },
    userID: { type: Number },
    price: { type: Number },
    broken: { type: Number },
    written: { type: Number },
    picture: { type: String },
  },
  computed: {
    schoolFormatted () {
      return this.formatSchool(this.school)
    },
    brokenFormatted () {
      return this.broken + '%'
    },
    writtenFormatted () {
      return this.written + '%'
    },
    pictureFormatted () {
      return server.default.server + this.picture;
    }
  },
  methods: {
    showMarketDetail () {
      this.$bus.$emit('showMarketDetail', this.itemID);
    }
  },
}
</script>

<style scoped>
.item {
  margin-left: 10%;
  width: 80%;
}
.card {
  border-radius: 20px;
}
.content {
  display: flex;
  justify-content: space-around;
}
.left {
  text-align: left;
  margin-left: 15px;
  width: 100%;
}
.name {
  font-size: 30px;
  margin-top: 10px;
}
.class {
  font-size: 20px;
  margin-top: 20px;
}
.status {
  font-size: 20px;
  margin-top: 20px;
}
.statusElement {
  margin-right: 20px;
}
.statusImage {
  width: 25px;
  position: relative;
  top: 7px;
}
.picture .el-image {
  width: 130px;
  height: 150px;
}
.open {
  font-size: 20px;
  margin-top: 30px;
  margin-bottom: 10px;
  cursor: pointer;
  color: rgb(61, 87, 235);
}
</style>